import React, { Component } from "react"
import axios from "axios"
import { connect } from "react-redux"
import * as actions from "../actions"
class Details extends Component {
  state = {
    data: null
  }
  UNSAFE_componentWillMount = async () => {
    const res = await axios.get(
      `https://api.coindesk.com/v1/bpi/currentprice/${this.props.code}.json`
    )
    this.setState({
      data: res.data.bpi
    })
    console.log(res.data.bpi)
  }
  render() {
    return (
      <div className="row">
        <div className="col-md-6">
          <div className="price-card">
            <h1>{this.props.code}</h1>
            <h3>{this.state.data && this.state.data[this.props.code].description}</h3>
            <span class="badge badge-danger">Danger</span><h2>{this.state.data && this.state.data[this.props.code].rate}</h2>
          </div>
        </div>
        <div className="col-md-6">
          <div className="price-card">
            <h1>USD</h1>
            <h3>{this.state.data && this.state.data.USD.description}</h3>
            <span class="badge badge-danger">Danger</span><p>{this.state.data && this.state.data.USD.rate}</p>
          </div>
        </div>
        <button className="btn btn-lg btn-block btn-warning" onClick={() => this.props.changeCountry(null)}>back</button>
      </div>
    )
  }
}

export default connect(
  null,
  actions
)(Details)
